
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    background: #ffd85c;
    width: 100%;
    min-height: 100vh;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
}
.panel {
    margin-top: 30px;
}
  
  /* Info Board */
.info_board {
    height: 100px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    margin: 10px 0px;
    font-weight: 900;
    text-shadow: 2px 0px white;
    background: rgba(255, 255, 255, 0.37);
}
.role_wrapper {
    height: 100px;
    width: 100px;
    font-weight: 100px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.info_text {
    font-size: 2.5em;
    /* letter-spacing: 10px; */
}
  
  /* Main Wrapper*/
.block_wrapper {
    width: 600px;
    height: 600px;
    display: grid;
}
@media only screen and (max-width: 600px) {
.block_wrapper {
      width: calc(100vw - 20px);
      height: calc(100vw - 20px);
}
}
  
  /* Tic Tac Toe item */
.game_item{
    outline: none;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    color: white;
    width: 100%;
    height: 100%;
    background: rgb(255, 153, 171);
    cursor: pointer;
}
.game_item:hover{
    background: rgb(252, 130, 152);
}
  
  /* Restart Button */
.restart_button {
    width: 100%;
    line-height: 50px;
    font-size: 1.5em;
    letter-spacing: 1px;
    background: white;
    cursor: pointer;
    outline: none;
    margin: 10px 0px;
    padding: 5px 0px;
    border-radius: 5px;
    border: none;
    -webkit-transition: all 0.25s ease-out;
    transition: all 0.25s ease-out;
    font-weight: 900;
    text-shadow: 2px 0px white;
}
.restart_button:hover {
    -webkit-box-shadow: 0 10px 20px 0 black;
            box-shadow: 0 10px 20px 0 black;
    -webkit-transform:  translateY(-1px);
            transform:  translateY(-1px);
    color: brown;
}
  
  /* Setting */
.setting_group {
    display: grid;
    grid-template-columns: repeat(2,1fr);
    grid-template-rows: repeat(1, 40px);
    font-weight: 900;
    text-shadow: 2px 0px white;
}
.setting_group select {
    font-size: 1.2em;
    outline: none;
    background: white;
}

